* {
  margin: 0;
  padding: 0;
  font-family: "Roboto Mono", monospace;
}

html {
  scroll-behavior: smooth;
}

body {
  width: 100%;
  overflow-x: hidden;
}

.logo {
  width: 66px;
  margin: 14px;
  position: absolute;
  right: 14px;
}

#home {
  height: 84vh;
  background-color: #f6d2b3;
}

#menu {
  width: 40px;
  position: absolute;
  top: 5vh;
  left: 4.5vw;
  cursor: pointer;
}

#menubar {
  /* display: none; */
  background-color: aliceblue;
  position: fixed;
  top: 0;
  left: -60vw;
  height: 100vh;
  width: 22%;
  z-index: 10;
  transition: all 0.1s ease-in;
}

#menubar #close {
  width: 30px;
  position: relative;
  top: 6vh;
  left: 17vw;
  cursor: pointer;
}

#menubar a {
  font-size: 26px;
  font-family: "Sansita Swashed", sans-serif;
  text-decoration: none;
  list-style: none;
  color: #000;
}

#menubar ul {
  margin-top: 16vh;
  margin-left: 3vw;
}

#menubar ul li {
  margin-top: 20px;
}

.img1,
.img2 {
  margin: 0 12%;
}

.img1 {
  width: 25%;
  margin-left: 20vw;
}

.img2 {
  width: 22%;
  margin-left: 1.8vw;
}

.main p {
  position: absolute;
  font-family: "Roboto Mono", monospace;
  margin-top: 1%;
}

.main .p {
  font-size: 24px;
  left: 20.5%;
}

.main .p2 {
  font-size: 19.8px;
  left: 59%;
}

