#products {
  background-color: rgba(250, 255, 240);
  overflow-x: hidden;
}

#products h2 {
  /* margin: 20%, 0; */
  text-align: center;
  font-family: "Roboto Mono", monospace;
  font-size: 32px;
}

#products ._1,
#products ._2 {
  padding: 4% 0;
}

#products ._3 {
  padding-bottom: 4%;
}

#products .sec1,
#products .sec2 {
  position: relative;
  margin-left: 2vw;
  text-align: left;
  display: flex;
  height: 340px;
  flex-direction: row;
  /* overflow-x: hidden; */
  transition: all 0.4s ease;
}

#products .sec1 Link,
#products .sec2 Link {
  margin-top: 35px;
  height: min-content;
  width: min-content;
  /* transform: translateY(-50%); */
}

.canvas img,
.pencile img,
.frame img,
.customize img {
  width: 120px;
}

.digital img {
  width: 110px;
}

#products .sec1 {
  /* margin: 0 6%; */
  margin-bottom: 5%;
  width: min-content;
}

.canvas .sec1 {
  /* position: relative;
    left: 5vw; */
  margin-bottom: 5%;
  width: min-content;
}

.pencile .sec1,
.pencile .sec2 {
  margin: 0 1vw;
}

.main2_,
.main3_,
.main5_ {
  margin: 0 1.6vw;
  margin-top: 34px;
  font-size: 16px;
  width: min-content;
  height: min-content;
  text-decoration: none;
  color: #000;
  font-family: "Ubuntu", sans-serif;
}

