.con1,
.con2,
.con3,
.con4,
.con5,
.con6,
.con7,
.con8 {
  background-color: aliceblue;
  height: min-content;
  width: 100%;
}

#products .back,
#products .next {
  width: 70px;
  /* height: 70px; */
  position: absolute;
  margin-top: 20%;
  /* transform: translateY(-50%); */
  cursor: pointer;
  /* opacity: 0.9; */
  z-index: 20;
  display: none;
}

#products .back_,
#products .next_ {
  width: 40px;
  /* height: 70px; */
  position: absolute;
  margin-top: 11%;
  transform: translateY(-50%);
  cursor: pointer;
  /* opacity: 0.9; */
  z-index: 20;
  /* display: none; */
}

/* #products .con5 .back, #products .con5 .next {
    top: 100%;
