} */

.back {
  left: 2.5vw;
  display: none;
}

.next {
  right: 2.5vw;
}

.back_ {
  left: 1.4vw;
  display: none;
}

.next_ {
  right: 1.4vw;
}

#products small {
  color: #63a007;
}

.desc p {
  padding-bottom: 8px;
}

.more {
  margin-bottom: 100px;
  width: 170px;
  height: 40px;
  background-color: transparent;
  border: 2px solid orangered;
  color: orangered;
  border-radius: 8px;
  font-size: 22px;
  position: relative;
  left: 50%;
  transform: translateX(-50%);
  cursor: pointer;
  overflow: hidden;
  transition: all 0.5s ease-in-out;
}

.more pre {
  font-family: "Sansita Swashed", sans-serif;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.more .hide {
  margin-left: -100%;
  width: 100%;
  height: 100%;
  background-color: orangered;
  transition: all 0.5s ease-in-out;
}

.more:hover .hide,
.more:hover {
  margin-left: 0;
  color: white;
}

