#header {
  width: 100%;
  height: 30vh;
}

.logo_ {
  position: absolute;
  right: 30px;
  margin-top: 15px;
  width: 66px;
}

h3 {
  position: absolute;
  top: 14px;
  margin-left: 42%;
  display: inline-flex;
  font-family: "Roboto Nono", sans-serif;
  font-size: 44px;
  background: linear-gradient(to right, #f0005c, #ffea00, #008cff, #ff00eb);
  -webkit-text-fill-color: transparent;
  background-clip: border-box;
  -webkit-background-clip: text;
}

.hpre {
  position: absolute;
  margin-left: 38.5%;
  top: 60px;
  font-family: "Dancing Script", cursive;
  color: #000;
  font-size: 44px;
}

@media only screen and (max-width: 500px) {
  .logo_ {
    width: 48px;
    margin-top: 2.3vh;
    right: 15px;
  }

  h3 {
    top: 20px;
    margin-left: 27%;
    font-size: 1.8em;
  }

  .hpre {
    margin-left: 19%;
    top: 50px;
    font-size: 1.7em;
  }
}

@media only screen and (max-width: 900px) and (min-width: 500px) {
  .logo_ {
    margin-top: 18px;
    margin-left: 34px;
    width: 84px;
  }

  h3 {
    position: absolute;
    top: 22px;
    margin-left: 34%;
    display: inline-flex;
    font-family: "Roboto Nono", sans-serif;
    font-size: 3em;
  }

  .hpre {
    position: absolute;
    margin-left: 27%;
    top: 76px;
    font-family: "Dancing Script", cursive;
    color: #000;
    font-size: 3em;
  }
}
