body {
  background: aliceblue;
  overflow-x: hidden;
}

.goback {
  width: 80px;
  color: #0255a3;
  font-size: 24px;
  font-family: "Sansita Swashed", sans-serif;
  text-decoration: none;
  margin-left: 4vw;
  cursor: pointer;
}

#main {
  background: aliceblue;
  width: 100vw;
  margin-top: -40px;
}

#main h2 {
  /* position: relative; */
  padding-top: 14px;
  text-align: center;
}

.products {
  display: flex;
  flex-wrap: wrap;
  margin: 0 3.3vw;
  margin-top: 60px;
}

img {
  width: 120px;
}

.main1,
.main2,
.main3,
.main5,
.main6 {
  width: min-content;
  height: min-content;
  margin: 2vh 2.2vw;
  text-decoration: none;
  color: #000;
  font-family: "Ubuntu", sans-serif;
  font-size: 16px;
}

.desc p {
  padding-bottom: 8px;
  position: relative;
}

.desc small {
  color: #63a007c9;
}
.btnCon {
  width: 90%;
  position: relative;
  margin: 50px 0;
  margin-left: 50%;
  transform: translateX(-50%);
  display: flex;
  justify-content: space-between;
}

/* Mobiles Screen */
@media only screen and (max-width: 500px) {
  .goback {
    color: #0255a3;
    font-size: 1.2em;
  }

  #main {
    margin-top: -80px;
  }
  #main h2 {
    padding-top: 4px;
    font-size: 20px;
  }

  .products {
    display: flex;
    flex-wrap: wrap;
    margin: 0 3vw;
    margin-top: 34px;
  }

  img {
    width: 120px;
  }

  .main1,
  .main2,
  .main3,
  .main5,
  .main6 {
    width: 90px;
    font-size: 1.1em;
    margin: 2vh 12vw 2vh 7.5vw;
  }

  .btnCon {
    width: 80%;
    height: 12vh;
  }
}

/* Tab Screen */
@media only screen and (max-width: 900px) and (min-width: 500px) {
  .goback {
    font-size: 2.2em;
  }

  #main {
    margin-top: -150px;
  }

  #main h2 {
    position: absolute;
    margin-left: 50%;
    transform: translateX(-50%);
    font-size: 2.4em;
  }

  .products {
    margin: 0 3vw;
    margin-top: 150px;
  }

  img {
    width: 200px;
  }

  .main1,
  .main2,
  .main3,
  .main5,
  .main6 {
    width: min-content;
    margin: 2vh 3vw;
    font-size: 2em;
  }
}
