body {
  width: 100%;
  background-color: aliceblue;
  /* overflow: hidden; */
}

.main_ {
  background-color: aliceblue;
  margin: -14vh 0 8vh 0.5vw;
}

.goback_ {
  width: 80px;
  margin-left: 3vw;
  color: #0255a3;
  /* font-weight: bold; */
  font-family: "Sansita Swashed", sans-serif;
  font-size: 28px;
  text-decoration: none;
  position: relative;
  top: 2vh;
  cursor: pointer;
}

.buttons {
  position: relative;
  margin-left: 35%;
  margin-top: -30vh;
}

.buttons_ {
  position: relative;
  margin-left: 35%;
  margin-top: -47vh;
}

.buy-btn {
  margin: 40px 0 80px 0;
  width: 170px;
  height: 40px;
  background-color: transparent;
  border: 2px solid orangered;
  color: orangered;
  border-radius: 8px;
  font-size: 22px;
  cursor: pointer;
  /* overflow: hidden; */
  transition: all 0.4s ease-in-out;
}

