.buy-btn:hover {
  background-color: orangered;
  color: aliceblue;
}

.preview_right {
  position: relative;
  left: 3.7vw;
  top: 14vh;
  margin-bottom: 30vh;
  text-align: center;
  width: 310px;
}

.right2 img {
  position: relative;
  width: 88px;
  left: 1.3vw;
  /* display: inline-flex; */
  cursor: pointer;
}

#_1 {
  margin-left: 1vw;
}

#_2 {
  margin-left: 1vw;
  opacity: 0.6;
}

.preview {
  display: flex;
  margin-top: 10vh;
  margin-left: 80px;
}

.preview .left img {
  width: 300px;
}

#img1 {
  display: block;
}

#img2 {
  display: none;
}

.desc_ {
  position: absolute;
  top: 30vh;
  margin-left: 34vw;
}

.desc_ h1 {
  font-size: 34px;
}

.desc_ p {
  font-size: 22px;
}

.desc_ .rs {
  padding-top: 20px;
  font-size: 28px;
  color: #487701;
}

/* Mobiles Screen */
@media screen and (max-width: 500px) {
  .goback_ {
    margin-left: 22px;
    font-size: 22px;
  }

  .main_ {
    margin-top: -14vh;
    margin-left: 0;
  }

  .buttons {
    margin-left: 50%;
    transform: translateX(-50%);
    margin-top: 0;
  }

  .buttons_ {
    margin-left: 50%;
    transform: translateX(-50%);
    margin-top: 0;
  }

  .preview {
    width: 100%;
    display: flex;
    /* flex-direction: row; */
    margin-top: 2vh;
    margin-left: 3vw;
  }

  .preview_right {
    position: relative;
    left: 1.5vw;
    width: 330px;
  }

  .right2 img {
    width: 74px;
    /* left: 28%; */
    margin-top: 0;
    cursor: pointer;
  }

  #_2 {
    margin-left: 2vw;
    /* top: 12vh; */
    cursor: pointer;
    opacity: 0.6;
  }

  .preview .left img {
    margin-left: 10%;
    margin-top: 5vh;
  }

  .preview .left a {
    width: min-content;
  }

  #img1 {
    width: 280px;
    display: block;
  }

  #img2 {
    width: 280px;
    /* margin-top: 8vh; */
    display: none;
  }

  .desc_ {
    margin: 0;
    position: relative;
    top: -8vh;
    left: 3vw;
  }

  .desc_ h1 {
    font-size: 28px;
  }

  .desc_ p {
    font-size: 23px;
  }

  .desc_ .rs {
    font-size: 26px;
    color: #487701;
  }
}

/* Tab Screens */

@media only screen and (max-width: 1200px) and (min-width: 500px) {
  .main_ {
    margin-top: 4%;
    margin-left: 3vw;
  }

  .preview {
    width: 100%;
    display: flex;
    /* flex-direction: row; */
    margin-top: 2vh;
    margin-left: 3vw;
  }

  .preview_right {
    position: absolute;
    top: 0;
    left: 60vw;
  }

  .right2 img {
    position: absolute;
    width: 120px;
    cursor: pointer;
  }

  #_1 {
    margin-top: 23.5vh;
    /* top: 11vh; */
  }

  #_2 {
    margin-top: 34.5vh;
    /* top: 12vh; */
    opacity: 0.6;
  }

  .preview .left img {
    margin-left: 2%;
    margin-top: 5vh;
  }

  .preview .left a {
    width: min-content;
  }

  #img1 {
    width: 400px;
    display: block;
  }

  #img2 {
    width: 400px;
    display: none;
  }

  .desc_ {
    margin: 0;
    position: relative;
    bottom: 0;
    top: 6vh;
    left: 0;
  }

  .desc_ h1 {
    font-size: 42px;
  }

  .desc_ p {
    font-size: 34px;
  }

  .desc_ .rs {
    font-size: 42px;
    color: #487701;
  }
}
